.home {
    background-color: var(--body-color);
}

.home__container {
    row-gap: 7rem;
}

.home__content{
    grid-template-columns: 116px repeat(2, 1fr); 
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center ;
}

.home__social {
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem; 
}
.home__social-icon {
    font-size: 1.25rem;
    color: var(--title-color);

    &:hover {
        color: var(--title-color-dark)
    }
}

.home__data {
    position: relative;
}

.home__title {
    display: flex;
    font-size: var(--big-font-size);
    margin-bottom: var(--mb-0-25);
    white-space: nowrap;
}


.home__subtitle{
    position: relative;
    font-size: var(--h3-font-size);
    padding-left: 5.4rem;
    font-weight: var(--font-normal);
    margin-bottom: var(--mb-1);

    &::before{
        content: '';
        position: absolute;
        width: 70px;
        height: 1px;
        background-color: var(--text-color);
        left: 0;
        top: 1rem;
    }
}

.home__subtitle-icon {
    width: 10px;
    filter: drop-shadow(2px 2px 5px var(--drop-shadow));
    overflow: visible;
}

.animated-cloud {
    animation: swing 5.3s infinite;
    animation-timing-function: cubic-bezier(0.5, 0, 0.5, 1);
}

.animated-cloud_right{
    animation: swing 5.3s infinite;
    animation-delay: 0.5s;
    animation-timing-function: cubic-bezier(0.5, 0, 0.5, 1);
}

@keyframes swing {
    0% { transform: rotate(0deg); }
    25% { transform: rotate(3deg); }
    50% { transform: rotate(-3deg); }
    75% { transform: rotate(3deg); }
    100% { transform: rotate(0deg); }
}

.home__description {
    max-width: 400px;
    margin-bottom: var(--mb-3);
}

.home__img {
    background: url(../..//assets/img/profile.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: inset 0 0 0 9px rgba(255, 255, 255, 0.3);
    order: 1;
    justify-self: center;
    width: 300px;
    height: 300px;
    animation: profile__animate-border 8s ease-in-out infinite;
    transition: opacity 0.5s ease;
}

@keyframes profile__animate-border {
    0% {
        border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
    }

    50% {
        border-radius: 30% 60% 70% 40% / 50% 60% 30% 60%;
    }

    100% {
        border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
    }
}


.home__scroll {
    margin-left: 9.25rem;
}

.wheel {
    animation: scroll 2s ease infinite;
}

@keyframes scroll {
    0% {
        transform: translateY(0);
    }

    30% {
        transform: translateY(3.75rem);
    }
}

.home__scroll-name {
    color: var(--title-color);
    font-weight: var(--font-medium);
    margin-right: var(--mb-0-25);
    margin-left: 2px;
}

.home__scroll-arrow {
    font-size: 1.25rem;
    color: var(--title-color);
}

@media screen and (max-width: 992px) {
    .home__content {
        grid-template-columns: 100px repeat(2, 1fr);
        column-gap: 1.25rem;
    }

    .home__subtitle-icon {
        width: 8px;
    }

    .home__subtitle{
        padding-left: 3.75rem;
        margin-bottom: var(--mb-1);

        &::before {
            width: 42px;
            top: 0.8rem
        }
    }

    .home__description {
        max-width: initial;
        margin-bottom: var(--mb-2-5);
    }

    .home__img {
        width: 250px;
        height: 250px;
        box-shadow: inset 0 0 0 8px rgba(255, 255, 255, 0.3); 
    }

    .home__scroll {
        margin-left: 7.5rem;
    }
}

@media screen and (max-width: 768px) {
    .home__content {
        grid-template-columns: 0.5fr 3fr;
        padding-top: 3.5rem;
    }

    .home__img {
        width: 200px;
        height: 200px;
        order: initial;
        justify-self: initial;
        box-shadow: inset 0 0 0 6px rgba(255, 255, 255, 0.3); 
    }

    .home__data {
        grid-column: 1/3;
    }

    .home__scroll {
        display: none;
    }
}

@media screen and (max-width: 600px) {
    .home__title {
        font-size: 2.5rem;
    }
}   